.display1{
  font-weight: 700;
  font-size: 45px;
  line-height: 118%;
  font-family: $font2;
}

.display2{
  font-weight: 700;
  font-size: 30px;
  line-height: 150%;
  color: rgba($black,.7);
}

.display3{
  font-weight: 700;
  font-size: 20px;
  line-height: 150%;
  color: rgba($black,.7);
}

.body-text{
  font-size: 14px;
  line-height: 150%;
  color: rgba($black,.7);
  &--big{
    font-size: 15px;
  }
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 22px;
    }
  }
  strong{
    font-weight: 700;
  }
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 25px;
    line-height: 120%;
  }

  .body-text{
    font-size: 13px;
    &--big{
      font-size: 14px;
    }
  }
}
