.main{
  padding: 27px 0 43px;
  &__top{
    border-radius: 5px;
    overflow: hidden;
    background: #FFFFFF;
  }
  &__banner{
    padding: 67px 42px 47px;
    position: relative;
    background-image: url(../img/main-bg.svg);
    background-size: cover;
  }
  &__info{
    position: relative;
    z-index: 3;
    max-width: 600px;
    color: #fff;
  }
  &__desc{
    margin-top: 13px;
    font-size: 20px;
    line-height: 140%;
  }
  &__tabs{
    padding: 30px 35px;
    position: relative;
    z-index: 2;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    height: calc(100% + 108px);
    pointer-events: none;
  }
}

.catalog{
  background: #FFFFFF;
  border-radius: 5px;
  overflow: hidden;
  margin-top: 28px;
  table{
    border-collapse: collapse;
  }
  &__cards{
    display: none;
  }
}

.what{
  padding: 43px 0;
  &__inner{
    display: grid;
    grid-template-columns: 40% calc(60% - 44px);
    gap: 10px 44px;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    margin-top: 23px;
    font-size: 15px;
  }
}

.advantages{
  padding: 43px 0 60px;
  text-align: center;
  &__items{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 30px;
    margin-top: 32px;
  }
  &__icon{
    height: 40px;
    margin-bottom: 22px;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc{
    margin-top: 5px;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 0 0 30px;
    .container{
      padding: 0;
    }
    &__top{
      background-color: transparent;
    }
    &__banner{
      padding: 58px 35px;
    }
    &__desc{
      margin-top: 13px;
      font-size: 15px;
    }
    &__img{
      height: 100%;
      right: -7%;
      opacity: 0.2;
    }
    &__tabs{
      padding: 6px 20px 20px;
    }
  }

  .catalog{
    margin-top: 15px;
    &__table{
      display: none;
    }
    &__cards{
      display: block;
    }
  }

  .what{
    padding: 30px 0;
    &__inner{
      grid-template-columns: 100%;
      position: relative;
      padding-top: 45px;
    }
    &__title{
      position: absolute;
      top: 0;
      left: 0;
    }
    &__img{
      max-width: 75%;
    }
    &__text{
      margin: 0;
      font-size: 14px;
    }
  }

  .advantages{
    padding-top: 30px;
    &__items{
      grid-template-columns: 1fr 1fr;
    }
    &__icon{
      margin-bottom: 14px;
    }
    &__desc{
      margin-top: 14px;
    }
  }
}


@media screen and (max-width: 600px){
  .advantages{
    &__items{
      grid-template-columns: 1fr;
    }
    &__item{
      max-width: 270px;
      margin: 0 auto;
    }
  }
}


@media screen and (max-width: 450px){
  .main{
    &__img{
      right: -70px;
    }
  }
}
