.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-primary{
    font-family: $font2;
    font-weight: 700;
    color: #fff;
    background-color: $primary;
    &:hover{
        background-color: darken($primary,10%);
    }
    &:active{
        background-color: darken($primary,15%);
    }
}

.mbtn-gray{
    background: rgba(216, 216, 216, 0.55);
    color: rgba($black,.8);
    &:hover{
        color: #fff;
        background: $black;
    }
}
