@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/_fonts.scss';

.tabs{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.tab{
    padding: 0 10px;
    font-size: 15px;
    color: rgba($black,.6);
    height: 38px;
    background: #E0E0E0;
    border-radius: 5px;
    display: flex;
    align-items: center;
    margin: 10px 10px 0 0;
    transition: all .3s;
    cursor: pointer;
    &--active,
    &:hover{
        color: #fff;
        background: $black;
    }
    &--active{
        pointer-events: none;
    }
}

.form-group {
    position: relative;
    margin: 0;
    margin-bottom: 30px;
    input {
        border: none;
        border-bottom: 1px solid #000000;
        padding: 0 10px;
        height: 47px;
        width: 100%;
        font-size: 18px;
        background-color: transparent;
        color: #000;
        &::placeholder {
            color: #000;
        }
        &:focus {
            &::placeholder {
                color: rgba(#000, 0.3);
            }
        }
    }
}

.check {
    position: relative;
    input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        cursor: pointer;
        &:checked+label {
            &::before {
                // background-color: $green;
                border: 3px solid #fff;
            }
        }
        &:disabled+label {
            color: #AAAAAA;
            pointer-events: none;
        }
    }
    label {
        padding-left: 40px;
        position: relative;
        min-height: 20px;
        font-size: 14px;
        line-height: 20px;
        cursor: pointer;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            border-radius: 2px;
            background: rgba(255, 255, 255, 0.01);
            border: 1px solid rgba(255, 255, 255, 0.45);
            width: 20px;
            height: 20px;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            transition: all .100s;
        }
    }
}
