.footer{
  padding: 33px 0;
  background: #131313;
  color: #FFFFFF;
  &__inner{
    display: flex;
  }
  &__col{
    margin-right: 32px;
    &:nth-child(1){
      margin-right: 75px;
    }
    &:last-child{
      margin-left: auto;
      margin-right: 0;
    }
  }
  &__info{
    margin-top: 21px;
    max-width: 292px;
    font-size: 12px;
    line-height: 15px;
    opacity: 0.46;
  }
  &__title{
    font-size: 12px;
    line-height: 15px;
    opacity: 0.5;
  }
  &__link{
    display: block;
    margin-top: 17px;
    font-size: 15px;
    line-height: 18px;
    color: rgba(#fff,.67);
    &:hover{
      color: #fff;
    }
  }
  &__logo2{
    width: 175px;
  }
}


@media screen and (max-width: 1100px){
  .footer{
    &__col {
      &:nth-child(1) {
        margin-right: 32px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-bottom: 54px;
    &__inner{
      display: block;
    }
    &__col{
      margin: 0 0 42px;
      &:nth-child(1) {
        margin-right: 0;
      }
      &:last-child{
        margin: 54px 0 0;
      }
    }
    &__link{
      color: #fff;
    }
  }
}
