@import 'vars';
html {
    width: 100%;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding-top: 97px;
}

.page {
    font-family: $font1;
    font-weight: 400;
    color: $black;
    background-color: #F5F5F5;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    max-width: 1220px;
    margin: 0 auto;
    padding: 0 30px;

}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px) {
    .content{
        padding-top: 87px;
    }

    .container{
        padding: 0 20px;
    }
}
