.logo{
  display: block;
  width: 187px;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 21.5px 0;
  background-color: #fff;
  box-shadow: 0px 22px 66px rgba(0, 0, 0, 0.1);
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    margin-right: 40px;
  }
  &__mob{
    flex-grow: 1;
    max-width: 860px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    border-radius: 5px;
    height: 54px;
    padding: 0 20px;
    font-size: 16px;
    svg{
      margin-right: 10px;
    }
  }
  &__burger{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 14px;
    line-height: 17px;
    opacity: .4;
    &:not(:last-child){
      margin-right: 30px;
    }
    &--active{
      opacity: .7;
    }
    &:hover{
      opacity: 1;
      color: $black;
    }
  }
}


@media screen and (max-width: 1100px){
  .header{
    &__logo{
      width: 150px;
    }
  }

  .nav{
    &__link{
      &:not(:last-child){
        margin-right: 10px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__mob{
      position: fixed;
      width: 100%;
      height: calc(100vh - 85px);
      opacity: 0;
      pointer-events: none;
      top: 85px;
      left: 0;
      background-color: #fff;
      max-width: unset;
      padding: 20px;
      display: block;
      overflow-y: auto;
      transition: all .3s;
    }
    &__burger{
      width: 40px;
      height: 34px;
      display: flex;
      align-items: center;
      position: relative;
      z-index: 15;
      span{
        display: block;
        width: 100%;
        height: 6px;
        background: #6635CF;
        left: 0;
        border-radius: 2px;
        transition: all .3s;
        &:nth-child(1){
          position: absolute;
          top: 0;
        }
        &:nth-child(3){
          position: absolute;
          bottom: 0;
        }
      }
    }
    &__btn{
      margin-top: 40px;
    }
    &--active{
      .header{
        &__burger{
          span{
            &:nth-child(1){
              transform: rotate(-45deg) translate(-11px,9px);
            }
            &:nth-child(2){
              transform: scale(0);
            }
            &:nth-child(3){
              transform: rotate(45deg) translate(-11px,-9px);
            }
          }
        }
        &__mob{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }

  .nav{
    display: flex;
    flex-direction: column;
    align-items: center;
    &__link{
      margin: 0 auto 30px 0 !important;
      font-size: 22px;
    }
  }
}

