.catalog-card{
  &:not(:last-child){
    td{
      border-bottom: 0.5px solid rgba($black,.1);
    }
  }
  td{
    padding: 13.2px 0;
    &:nth-child(1){
      width: 50px;
    }
    &:nth-child(2){
      padding-left: 10px;
      padding-right: 10px;
      min-width: 161px;
    }
    &:nth-child(3){
      padding-left: 21px;
      padding-right: 21px;
      width: 350px;
    }
    &:nth-child(4){
      width: 422px;
    }
    &:nth-child(5){
      width: 184px;
      padding-left: 10px;
      padding-right: 10px;
    }
  }
  &__place{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    &-num{
      font-weight: 300;
      font-size: 24px;
      line-height: 29px;
    }
    &-title{
      font-size: 11.5862px;
      line-height: 14px;
    }
  }
  &__title{
    font-family: $font2;
    opacity: 0.8;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    text-transform: uppercase;
  }
  &__screen{
    display: block;
    width: 100%;
    max-width: 141px;
    margin-top: 10px;
  }
  &__feedback{
    display: flex;
    align-items: center;
    &-nums{
      font-size: 12px;
      line-height: 15px;
      text-decoration-line: underline;
      opacity: 0.5;
      margin-left: 11px;
      min-width: 90px;
      &:hover{
        opacity: 1;
        text-decoration: none;
        color: $black;
      }
    }
  }
  &__rating{
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    color: rgba($black,.8);
    img{
      margin-right: 4.14px;
      flex-shrink: 0;
    }
  }
  &__site{
    display: flex;
    align-items: center;
    padding: 0 15px;
    height: 32.55px;
    font-size: 13.2414px;
    border-radius: 4.13793px;
    margin: 0;
    flex-shrink: 0;
    white-space: nowrap;
  }
  &__status{
    &-item{
      display: flex;
      font-size: 12px;
      line-height: 16px;
      color: rgba($black,.5);
      &:not(:last-child){
        margin-bottom: 5px;
      }
    }
    &-icon{
      width: 16px;
      flex-shrink: 0;
      margin-right: 5px;
      img{
        width: 100%;
      }
    }
  }
  &__btn{
    margin-top: 16px;
    width: 100%;
    height: 32.55px;
    font-size: 13.2414px;
    border-radius: 4.13793px;
  }
  &__promo{
    width: 98px;
    display: block;
    &:not(:last-child){
      margin-bottom: 18px;
    }
    &:not(:first-child){
      margin-top: 7px;
    }
  }
  &--gold{
    .catalog-card__place,
    td:nth-child(1){
      background: linear-gradient(135.8deg, rgba(255, 219, 149, 0.66) -5.12%, rgba(255, 209, 166, 0.66) 75.35%);
    }
  }
  &--silver{
    .catalog-card__place,
    td:nth-child(1){
      background: linear-gradient(135.8deg, rgba(255, 255, 255, 0.66) -5.12%, rgba(234, 234, 234, 0.66) 75.35%);
    }
  }
  &--bronze{
    .catalog-card__place,
    td:nth-child(1){
      background: linear-gradient(135.8deg, rgba(255, 219, 149, 0.66) -5.12%, rgba(255, 209, 166, 0.66) 75.35%), linear-gradient(135.8deg, rgba(255, 255, 255, 0.66) -5.12%, rgba(234, 234, 234, 0.66) 75.35%);
    }
  }
}


@media screen and (max-width: 1100px){
  .catalog-card{
    td{
      &:nth-child(3){
        padding-left: 10px;
        padding-right: 10px;
        width: 300px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-card{
    padding: 17px 35px 20px 47px;
    position: relative;
    &:not(:last-child){
      border-bottom: 0.5px solid rgba($black,.1);
    }
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__place{
      position: absolute;
      left: 0;
      top: 0;
      width: 25px;
      &-title{
        display: none;
      }
    }
    &__feedback{
      margin-top: 7px;
      &-nums{
        min-width: unset;
      }
    }
    &__site{
      margin-left: auto;
    }
    &__text{
      margin-top: 14px;
    }
    &__btn{
      width: 132.79px;
    }
    &__promo{
      margin: 10px 0 !important;
    }
    &__screen{
      margin: 0;
    }
  }
}
